<!--
    table

    @prop {Object} tableData  
    @prop {Object} tableColumn  
    @prop {String} placeHolder placeholder ""
    @prop {String} operationLabel  ""
    @prop {String} isPaging  falsepageInfo
    @prop {Object} pageInfo   pageSize: 10, currPage: 1, totalRecord: 0

    @event sortChange 
    @event filterChange 
    @event refreshTable table
    @event btnAdd 
    @event inputQuery 

    @example

    <common-table
        :page-info="pageInfo"
        :table-data="tableData"
        :table-column="tableColumn"
        :operation-label="operationLabel"
        :place-holder="placeHolder"
        @sortChange="handleSortChange"
        @refreshTable="handleRefreshTable"
        @filterChange="handleFilterChange"
        @btnAdd="handleBtnAdd">
        <el-table-column label="" slot="opera">
            <template scope="scope">
                <el-button>{{scope.row.btnText}}</el-button>
            </template>
        </el-table-column>
    </common-table>

    table

    tableColumn: [
        {
            prop: 'eventName',
            label: ''
        },
        ...
        {
            slot: 'opera'
        }
    ]
     slotslot
-->
<template>
    <div class="common-table">
        <div class="table-operation clearfix">
            <el-input
                icon="search"
                :placeholder="placeHolder"
                class="input-search"
                v-model="inputText"
                @click="onQueryClick"
                @keyup.enter.native="onQueryClick">
            </el-input>
            <el-button
                type="primary"
                class="btn-add"
                @click="onBtnAddClick">
                {{operationLabel}}
            </el-button>
        </div>
        <el-table
            :data="tableDataCopy"
            @filter-change="filterChange"
            @sort-change="sortChange">
            <template v-for="(column,index) in tableColumn">
                <!--  -->
                <slot v-if="column.slot" :name="column.slot"></slot>
                <el-table-column
                    v-else
                    v-bind="column">
                </el-table-column>
            </template>
        </el-table>
        <div class="clearfix">
            <el-pagination
                layout="prev, pager, next"
                @current-change="handleCurrentChange"
                :current-page="pagination.currPage"
                :total="pagination.totalRecord"
                :page-size="pagination.pageSize">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            tableData: {
                type: Array,
                default () {
                    return [];
                }
            },
            tableColumn: {
                type: Array,
                default () {
                    return [];
                }
            },
            placeHolder: {
                type: String,
                default: ''
            },
            operationLabel: {
                type: String,
                default: ''
            },
            isPaging: {
                type: Boolean,
                default: false
            },
            pageInfo: {
                type: Object,
                default () {
                    return {
                        pageSize: 10,
                        currPage: 1,
                        totalRecord: 0
                    };
                }
            }
        },
        data () {
            return {
                inputText: '',
                filterType: '',
                order: {},
                pagination: {
                    pageSize: 10,
                    currPage: 1,
                    totalRecord: 0
                },
                tableDataCopy: []
            };
        },
        components: {},
        mounted() {},
        methods: {
            /**
             * 
             */
            handleCurrentChange (val) {
                this.pagination.currPage = val;
                if (this.isPaging) {
                    // 
                    this.tableDataCopy = this.tableData.slice(this.start, this.end);
                } else {
                    // 
                    this.$emit('refreshTable', {
                        'inputText': this.inputText,
                        'pagination': this.pagination,
                        'order': this.orderInfo,
                        'filterType': this.filterType
                    });
                }

            },
            /**
             * 
             */
            onQueryClick () {
                this.$emit('refreshTable', {
                    'inputText': this.inputText,
                    'pagination': this.pagination,
                    'order': this.orderInfo,
                    'filterType': this.filterType
                });
            },
            /**
             * 
             */
            onBtnAddClick () {
                this.$emit('btnAdd');
            },
            /**
             * 
             * @param {Object} val {columnKey, value(Array)} element
             */
            filterChange (val) {
                let key = Object.keys(val)[0];
                let status = val[key][0];
                this.filterType = status;
                this.$emit('refreshTable', {
                    'inputText': this.inputText,
                    'pagination': this.pagination,
                    'order': this.orderInfo,
                    'filterType': this.filterType
                });
                this.$emit('filterChange', val);
            },
            /**
             * 
             * @param {Object} params {column, prop, order} 
             */
            sortChange (params) {
                this.order = params;
                this.$emit('refreshTable', {
                    'inputText': this.inputText,
                    'pagination': this.pagination,
                    'order': this.orderInfo,
                    'filterType': this.filterType
                });
                this.$emit('sortChange', params);
            }
        },
        computed: {
            start () {
                return (this.pagination.currPage - 1) * this.pagination.pageSize;
            },
            end () {
                return this.start + this.pagination.pageSize;
            }
        },
        watch: {
            tableData: {
                handler(val) {
                    this.pagination.totalRecord = val.length;
                    if (this.isPaging) {
                        this.tableDataCopy = this.tableData.slice(this.start, this.end);
                    } else {
                        this.tableDataCopy = this.tableData.slice();
                        this.pagination = Object.assign({}, this.pageInfo);
                    }
                },
                immediate: true,
            }
        }
    };
</script>

<style media="screen">
  .clearfix {
    display: block;
    zoom: 1;
    &:after {
      content: "";
      display: block;
      font-size: 0;
      height: 0;
      clear: both;
      visibility: hidden;
    }
  }
  .el-pagination {
    display: inline-block;
    padding: 20px 0;
    float: right;
  }
</style>

<style lang="scss" scoped>
    .common-table {
        .table-operation {
            .input-search {
                width: 360px;
                margin-bottom: 20px;
            }
            .btn-add {
                float: right;
            }
        }
    }
</style>



